.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}


.calculator{
  border-radius: 20px;
  width: 25%;
  height: 80vh;
  background-color: 	#05014a;
}


.screen{
  display: flex;
  justify-content: flex-end;
  padding: 20px;
  color: #fff;
  width: calc(100% - 40px);
  height: 30vh;
  background-color: 	#020079;
  border: none;
  border-radius: 20px;
}

.buttons{
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 40vh;
  border: none;
  border-radius:20px ;
}

.button_wrap{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 25%;
  height: 100%;
  gap: 0;
  
}

.button_wrap span{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 25%;
  color: #fff;
}


